import { useEffect, useState } from "react";
import Modal from "../../../../Ui/Modal/Modal";
import style from "../../FeesStructure/FeesStructure.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import FeeStatisticsBankFilter from "../../FeeStatistics/FeeStatisticsBankFilter";
import SetButton from "../../../../Ui/Button/SetButton";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InstituteStudent from "../../../InstituteStudent/InstituteStudent";
import SelectMasterFeeHead from "../../FeesStructure/Structure/SelectMasterFeeHead";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import {
  useFinanceAddOtherFees,
  useFinanceAddOtherFeesNonExisting,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import SelectAllFinanceFeeStructure from "../../FeesStructure/SelectFeeStructure/SelectAllFinanceFeeStructure";
import SelectFeeStructureCard from "../../FeesStructure/SelectFeeStructure/SelectFeeStructureCard";
import FinanceOtherFeesCollect from "../FinanceOtherFeesCollect/FinanceOtherFeesCollect";
import SelectMasterOtherFeeHeadDropdown from "../../FeesStructure/Structure/SelectMasterOtherFeeHeadDropdown";
const AddOtherFees = ({
  onClose,
  fid,
  instituteId,
  onRefetch,
  whichOtherFees,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(0);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    students: [],
    other_fees_name: "",
    other_fees_type: "",
    bank_account: "",
    struct: "",
  });
  const [extra, setExtra] = useState({
    not_exist: false,
    name: "",
  });
  const [totalFeeHeadAmount, setTotalFeeHeadAmount] = useState(0);
  const [addApplicableFeeHead, setAddApplicableFeeHead] = useState([]);
  const [applicableFeeHeadId, setApplicableFeeHeadId] = useState([]);

  const [financeAddOtherFees] = useFinanceAddOtherFees();
  const [financeAddOtherFeesNonExisting] = useFinanceAddOtherFeesNonExisting();
  useEffect(() => {
    if (whichOtherFees) {
      setState((prev) => ({
        ...prev,
        other_fees_type: whichOtherFees,
      }));
    }
  }, [whichOtherFees]);
  useEffect(() => {
    if (addApplicableFeeHead) {
      let total = 0;
      for (let i = 0; i < addApplicableFeeHead?.length; i++) {
        total += +addApplicableFeeHead[i]?.head_amount;
      }
      setTotalFeeHeadAmount(total);
    }
  }, [addApplicableFeeHead]);
  const onInputChange = (e) => {
    setState((pre) => ({
      ...pre,
      [e.target.name]: e.target.value,
    }));
  };
  const onSelectBank = (value) => {
    setState((pre) => ({
      ...pre,
      bank_account: value,
    }));
  };
  const onFeeHeadLabelChange = (e, index) => {
    const input1 = [...addApplicableFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_name = e.target.value;
      }
    }
    setAddApplicableFeeHead(input1);
  };
  const onFeeHeadPriceChange = (e, index) => {
    const input1 = [...addApplicableFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_amount = +e.target.value;
      }
    }
    setAddApplicableFeeHead(input1);
  };

  const onStudent = () => {
    setStep(1);
  };

  const onCloseStep = () => {
    setStep(0);
  };

  const onSelectStudent = (val) => {
    if (state?.students?.includes(val?._id)) {
      setState((pre) => ({
        ...pre,
        students: pre?.students?.filter((id) => id !== val?._id),
      }));
    } else {
      setState((pre) => ({
        ...pre,
        students: [...pre?.students, val?._id],
      }));
    }
  };

  const onAddOtherFees = () => {
    let validation = requiredField(state, [
      "students",
      "struct",
      "other_fees_name",
    ]);
    if (
      existingRequiredField(validation) &&
      fid &&
      addApplicableFeeHead?.length > 0 &&
      state?.students?.length > 0
    ) {
      setDisabled((pre) => !pre);
      financeAddOtherFees({
        fid: fid,
        bodyParameter: {
          ...state,
          bank_account: state?.bank_account?._id,
          heads: addApplicableFeeHead,
          payable_amount: totalFeeHeadAmount,
          struct: null,
          is_collect: "No",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    } else {
      setErrorField(validation);
    }
  };
  const onProcessPayementOtherFees = () => {
    let validation = null;
    if (extra.not_exist) {
      validation = requiredField({ ...state, student_name: extra.name }, [
        "students",
        "struct",
        "other_fees_name",
      ]);
      if (
        existingRequiredField(validation) &&
        addApplicableFeeHead?.length > 0
      ) {
        setStep(4);
      } else {
        setErrorField(validation);
      }
    } else {
      validation = requiredField(state, [
        "students",
        "struct",
        "other_fees_name",
      ]);
      if (
        existingRequiredField(validation) &&
        addApplicableFeeHead?.length > 0 &&
        state?.students?.length === 1
      ) {
        setStep(4);
      } else {
        setErrorField(validation);
      }
    }
  };
  const onCollectOtherFees = (data) => {
    if (extra.not_exist) {
      if (fid && addApplicableFeeHead?.length > 0 && extra.name) {
        setDisabled((pre) => !pre);
        financeAddOtherFeesNonExisting({
          fid: fid,
          bodyParameter: {
            ...state,
            ...data,
            bank_account: state?.bank_account?._id,
            heads: addApplicableFeeHead,
            payable_amount: totalFeeHeadAmount,
            is_collect: "Yes",
            struct: null,
            student_name: extra.name,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch(() => {
            setDisabled((pre) => !pre);
          });
      }
    } else {
      if (
        fid &&
        addApplicableFeeHead?.length > 0 &&
        state?.students?.length > 0
      ) {
        setDisabled((pre) => !pre);
        financeAddOtherFees({
          fid: fid,
          bodyParameter: {
            ...state,
            ...data,
            bank_account: state?.bank_account?._id,
            heads: addApplicableFeeHead,
            payable_amount: totalFeeHeadAmount,
            is_collect: "Yes",
            struct: null,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch(() => {
            setDisabled((pre) => !pre);
          });
      }
    }
  };

  const onAddSubjectOtherFees = () => {
    let validation = requiredField(state, [
      "students",
      "bank_account",
      "other_fees_name",
    ]);
    if (
      existingRequiredField(validation) &&
      fid &&
      state?.struct?._id &&
      state?.students?.length > 0
    ) {
      setDisabled((pre) => !pre);
      financeAddOtherFees({
        fid: fid,
        bodyParameter: {
          ...state,
          bank_account: null,
          heads: [],
          payable_amount: state?.struct?.applicable_fees,
          struct: state?.struct?._id,
          is_collect: "No",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    } else {
      setErrorField(validation);
    }
  };
  const onProcessPayementSubjectOtherFees = () => {
    let validation = null;
    if (extra.not_exist) {
      validation = requiredField({ ...state, student_name: extra.name }, [
        "students",
        "bank_account",
        "other_fees_name",
      ]);
      if (existingRequiredField(validation) && state?.struct?._id) {
        setStep(5);
      } else {
        setErrorField(validation);
      }
    } else {
      validation = requiredField(state, [
        "students",
        "bank_account",
        "other_fees_name",
      ]);
      if (
        existingRequiredField(validation) &&
        state?.struct?._id &&
        state?.students?.length === 1
      ) {
        setStep(5);
      } else {
        setErrorField(validation);
      }
    }
  };
  const onCollectSubjectOtherFees = (data) => {
    if (extra.not_exist) {
      if (fid && state?.struct?._id && extra.name) {
        setDisabled((pre) => !pre);
        financeAddOtherFeesNonExisting({
          fid: fid,
          bodyParameter: {
            ...state,
            ...data,
            bank_account: null,
            heads: [],
            payable_amount: state?.struct?.applicable_fees,
            struct: state?.struct?._id,
            is_collect: "Yes",
            student_name: extra.name,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch(() => {
            setDisabled((pre) => !pre);
          });
      }
    } else {
      if (fid && state?.struct?._id && state?.students?.length > 0) {
        setDisabled((pre) => !pre);
        financeAddOtherFees({
          fid: fid,
          bodyParameter: {
            ...state,
            ...data,
            bank_account: null,
            heads: [],
            payable_amount: state?.struct?.applicable_fees,
            struct: state?.struct?._id,
            is_collect: "Yes",
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch(() => {
            setDisabled((pre) => !pre);
          });
      }
    }
  };
  const onSelectedStrucutre = (feeStructure) => {
    setState((pre) => ({
      ...pre,
      struct: feeStructure,
    }));
    onCloseStep();
  };

  const onSelectFeeMaster = (val) => {
    const objProperties = {
      head_name: val?.master_name,
      head_amount: val?.master_amount,
      master: val?._id,
      head_type: "BY_OTHER_FEES",
    };
    setAddApplicableFeeHead([objProperties]);
    setApplicableFeeHeadId([val?._id]);
  };

  // console.info("nabfsdf", addApplicableFeeHead);

  return (
    <>
      {step === 0 ? (
        <Modal onClose={() => {}}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(
                      whichOtherFees === "Normal Other Fees"
                        ? "add_normal_other_fees"
                        : whichOtherFees === "Subject Other Fees"
                        ? "add_subject_other_fees"
                        : "add_other_fees"
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
                marginBottom: "1rem",
              }}
            >
              <FormInput
                label={t("fees_name")}
                placeholder={t("fees_name_placeholder")}
                name={"other_fees_name"}
                value={state.other_fees_name}
                type={"text"}
                onChange={onInputChange}
                errorShow={errorField["other_fees_name"]}
              />

              {extra.not_exist ? (
                <FormInput
                  label={t("student_name")}
                  placeholder={t("student_name_placeholder")}
                  name={"student_name"}
                  value={state.name}
                  type={"text"}
                  onChange={(e) =>
                    setExtra((prev) => ({
                      ...prev,
                      name: e.target.value,
                    }))
                  }
                  errorShow={errorField["student_name"]}
                />
              ) : (
                <>
                  <SetButton
                    label="add_student"
                    count={state?.students?.length}
                    onAction={onStudent}
                  />

                  {state?.students?.length > 0 ? null : (
                    <SetButton
                      label="not_exist_student"
                      onAction={() => {
                        setExtra((prev) => ({
                          ...prev,
                          not_exist: !prev?.not_exist,
                        }));
                        setState((prev) => ({
                          ...prev,
                          students: [],
                        }));
                      }}
                      // customStyle={
                      //   extra.not_exist
                      //     ? {
                      //         backgroundColor: "var(--bg-button-hover)",
                      //       }
                      //     : null
                      // }
                    />
                  )}
                </>
              )}

              {whichOtherFees === "Subject Other Fees" ? null : (
                <FeeStatisticsBankFilter
                  fid={fid}
                  onSelect={onSelectBank}
                  errorShow={errorField["bank_account"]}
                  viewAs="NORMAL_FILTER"
                  defalutValue={
                    state?.bank_account?.finance_bank_account_name ?? ""
                  }
                />
              )}

              {state.other_fees_type === "Normal Other Fees" ? (
                <>
                  <SelectMasterOtherFeeHeadDropdown
                    fid={fid}
                    onSelect={onSelectFeeMaster}
                    errorShow={errorField["bank_account"]}
                    viewAs="NORMAL_FILTER"
                    // defalutValue={`${
                    //   addApplicableFeeHead?.[0]?.head_name ?? ""
                    // } - ${addApplicableFeeHead?.[0]?.head_amount ?? 0}`}
                  />
                  <div
                    style={{
                      paddingInline: "1rem",
                      marginTop: "1rem",
                    }}
                  >
                    <section
                      className={style.table_container}
                      style={{
                        margin: "0",
                      }}
                    >
                      <div className={style.table_heading}>
                        <h6>{t("fee_heads")}</h6>
                        <h6>{t("amount")}</h6>
                      </div>
                      <div className={style.table_body}>
                        {addApplicableFeeHead?.map((afh, index) => (
                          <div className={style.table_body_list} key={index}>
                            <input
                              type={"text"}
                              value={afh?.head_name}
                              onChange={(e) => onFeeHeadLabelChange(e, index)}
                              className={style.fee_head_name_input}
                            />
                            <input
                              type={"number"}
                              value={afh?.head_amount}
                              onChange={(e) => onFeeHeadPriceChange(e, index)}
                              className={style.fee_head_amount_input}
                            />
                          </div>
                        ))}
                      </div>
                      {/* <button
                        className={style.add_fees_head_btn}
                        title="Add Fees"
                        onClick={() => setStep(2)}
                        style={{
                          cursor: "pointer",
                          width: "106.4%",
                        }}
                      >
                        {t("add")}
                        <img
                          src={`${assestsUserFinanceUrl}/plus.svg`}
                          alt="add"
                        />
                      </button> */}
                      <div className={style.table_body}>
                        <div
                          className={style.table_body_list}
                          style={{
                            borderTopColor: "rgba(18, 18, 18, 0.5)",
                            borderTopWidth: "0.5px",
                            borderTopStyle: "solid",
                          }}
                        >
                          <h6 className={style.fees_total}>{t("subtotal")}</h6>
                          <h6 className={style.fees_total}>
                            {totalFeeHeadAmount}
                          </h6>
                        </div>
                      </div>
                    </section>
                  </div>
                </>
              ) : state.other_fees_type === "Subject Other Fees" ? (
                <>
                  {state.struct ? (
                    <SelectFeeStructureCard
                      structure={state.struct}
                      isShow="YES"
                      customContainerStyle={{
                        marginTop: "1rem",
                      }}
                    />
                  ) : (
                    <SetButton
                      label="select_fee_structure"
                      onAction={() => setStep(3)}
                    />
                  )}
                </>
              ) : null}

              {state.other_fees_type === "Normal Other Fees" ? (
                <div
                  style={
                    state?.students?.length === 1
                      ? {
                          display: "flex",
                          justifyContent: "space-between",
                          alignItems: "center",
                          gap: "1rem",
                        }
                      : {
                          display: "block",
                        }
                  }
                >
                  {extra.not_exist ? (
                    <CreateButton
                      label="collect_fees"
                      onAction={onProcessPayementOtherFees}
                    />
                  ) : (
                    <CreateButton label="request" onAction={onAddOtherFees} />
                  )}

                  {state?.students?.length === 1 ? (
                    <CreateButton
                      label="collect_fees"
                      onAction={onProcessPayementOtherFees}
                    />
                  ) : null}
                </div>
              ) : state.other_fees_type === "Subject Other Fees" ? (
                <div
                  style={
                    state?.students?.length === 1
                      ? {
                          display: "flex",
                          justifyContent: "space-between",
                          alignItems: "center",
                          gap: "1rem",
                        }
                      : {
                          display: "block",
                        }
                  }
                >
                  {extra.not_exist ? (
                    <CreateButton
                      label="collect_fees"
                      onAction={onProcessPayementSubjectOtherFees}
                    />
                  ) : (
                    <CreateButton
                      label="request"
                      onAction={onAddSubjectOtherFees}
                    />
                  )}

                  {state?.students?.length === 1 ? (
                    <CreateButton
                      label="collect_fees"
                      onAction={onProcessPayementSubjectOtherFees}
                    />
                  ) : null}
                </div>
              ) : null}
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      ) : step === 1 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onCloseStep}
          instituteId={instituteId}
          isMultiSelect
          onMultiSelect={onSelectStudent}
          onDone={onCloseStep}
          selectList={state.students}
        />
      ) : step === 2 ? (
        <SelectMasterFeeHead
          fid={fid}
          onClose={onCloseStep}
          setFeeHead={setAddApplicableFeeHead}
          setFeeHeadId={setApplicableFeeHeadId}
          feeHeadId={applicableFeeHeadId}
          isSegment="BY_OTHER_FEES"
          isOne={state.other_fees_type === "Normal Other Fees" ? true : false}
        />
      ) : step === 3 ? (
        <SelectAllFinanceFeeStructure
          title="select_fee_structure"
          onClose={onCloseStep}
          onSelectedStrucutre={onSelectedStrucutre}
          fid={fid}
          oneStep
        />
      ) : step === 4 ? (
        <FinanceOtherFeesCollect
          onClose={onCloseStep}
          onGrabCollectFees={onCollectOtherFees}
          directCollect="YES"
          applicant={{
            ...state,
            heads: addApplicableFeeHead,
          }}
          isOtherFees="OTHER_FEES"
        />
      ) : step === 5 ? (
        <FinanceOtherFeesCollect
          onClose={onCloseStep}
          onGrabCollectFees={onCollectSubjectOtherFees}
          directCollect="YES"
          applicant={{
            ...state,
            heads: addApplicableFeeHead,
          }}
          isOtherFees="SUBJECT_OTHER_FEES"
        />
      ) : null}
    </>
  );
};

export default AddOtherFees;
