import { useTranslation } from "react-i18next";
import Loading from "../../../../Loader/Loading";
import CustomSelectOtherFeeMaster from "../../../../Ui/Select/CustomSelectOtherFeeMaster";
import { useFinanceAllOtherFeeHeadMaster } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
const SelectMasterOtherFeeHeadDropdown = ({
  title,
  fid,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();

  const { financeAllOtherFeeHeadMaster, financeAllOtherFeeHeadMasterLoading } =
    useFinanceAllOtherFeeHeadMaster({
      data: {
        fid: fid,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !fid,
    });

  return (
    <>
      <CustomSelectOtherFeeMaster
        selectLabel={t(title ?? "select_other_fee_master")}
        selectedValue={t("select_other_fee_master_placeholder")}
        options={
          financeAllOtherFeeHeadMaster?.list?.length > 0
            ? financeAllOtherFeeHeadMaster?.list
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />

      {financeAllOtherFeeHeadMasterLoading && <Loading />}
    </>
  );
};

export default SelectMasterOtherFeeHeadDropdown;
