import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useFinanceUpdateOtherFees } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import UpdateOtherFees from "./AddOtherFees/UpdateOtherFees";
const FinanceOtherFeesCardMenu = ({ onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [financeUpdateOtherFees] = useFinanceUpdateOtherFees();
  const onStepClose = () => {
    setStep("");
  };
  const onEditAction = (data) => {
    if (menuData?._id) {
      onStepClose();
      setDisabled((pre) => !pre);
      financeUpdateOtherFees({
        ofid: menuData?._id,
        bodyParameter: {
          ...data,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <UpdateOtherFees
          onEditAction={onEditAction}
          menuData={menuData}
          onClose={onStepClose}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={() => setStep(1)}>{t("edit")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default FinanceOtherFeesCardMenu;
