import { useTranslation } from "react-i18next";
import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../TransactionHistory/TransactionHistory.module.css";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
const OtherTransactionHistoryCard = ({ history }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <>
      <div
        className={style.sidebarheader}
        style={{
          gap: "1%",
        }}
        title="View Receipt"
      >
        <div
          className={style.history_image_container}
          style={{
            width: "50%",
          }}
        >
          {history?.payment_student?.studentProfilePhoto ? (
            <Link
              to={`/q/${params.username}/student/profile`}
              state={{
                ...getQuery.state,
                sid: history?.payment_student?._id,
                // studentId: history?.payment_student?._id,
              }}
            >
              <img
                src={`${imageShowUrl}/${history?.payment_student?.studentProfilePhoto}`}
                alt="qr code"
                style={{
                  width: "60px",
                  height: "60px",
                  borderRadius: "12px",
                }}
              />
            </Link>
          ) : null}
          {history?.payment_student_detail ? (
            <div className={style.text}>
              <p className={style.rcv}>{t("recived_from")}</p>
              <h6>{history?.fee_receipt?.student_name ?? ""}</h6>
              <p className={style.rcv}>
                {t("batch")} {": "}
                {history?.payment_student_detail?.batch ?? ""},{" "}
                {t("student_class")}
                {": "}
                {history?.payment_student_detail?.classes}, {t("roll_number")}{" "}
                {": "}
                {history?.payment_student_detail?.roll_no ?? ""}
              </p>
            </div>
          ) : (
            <div className={style.text}>
              <p className={style.rcv}>{t("recived_from")}</p>
              <h6>
                {history?.payment_student
                  ? `${history?.payment_student?.studentFirstName ?? ""} ${
                      history?.payment_student?.studentMiddleName ?? ""
                    } ${history?.payment_student?.studentLastName ?? ""}`
                  : history?.payment_by_end_user_id?.userLegalName ??
                    history?.payment_by_end_user_id_name}
              </h6>
              <p className={style.rcv}>
                {t("gr_number")} {history?.payment_student?.studentGRNO ?? ""}
              </p>
              <p className={style.rcv}>
                {history?.payment_student?.fee_structure
                  ?.unique_structure_name ?? ""}
              </p>
            </div>
          )}
        </div>

        <div
          className={style.text}
          style={{
            width: "27%",
          }}
        >
          <p
            className={style.rcv}
            style={{
              width: "100%",
            }}
          >
            {t("transaction_id")}
          </p>
          <h6
            style={{
              width: "100%",
              wordWrap: "break-word",
            }}
          >
            {history?.razorpay_payment_id
              ? history?.razorpay_payment_id
              : history?.fee_receipt?.fee_utr_reference
              ? history?.fee_receipt?.fee_utr_reference
              : "N/A"}
          </h6>
          <p
            className={style.rcv}
            style={{
              width: "100%",
            }}
          >
            {dayjs(history?.created_at).format("DD MMM YYYY")}
            {", "}
            {localTimeFormat(history?.created_at, "LT")}
          </p>
        </div>
        <div
          className={style.text}
          style={{
            width: "20%",
          }}
        >
          <p
            className={style.rcv}
            style={{
              width: "100%",
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            {t("invoice_no")}{" "}
            {history?.fee_receipt?.invoice_count ??
              history?.payment_invoice_number ??
              "N/A"}
            {history?.fee_receipt?.receipt_file ? (
              <a
                href={`${pdfCdnUrl}/${history?.fee_receipt?.receipt_file}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={`${assestsUserFinanceUrl}/receipt.svg`}
                  alt="receipt icon"
                  title="Download Fees Receipt"
                />
              </a>
            ) : null}
          </p>

          <h6
            style={{
              width: "100%",
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            {t("rs_only")} {history?.payment_amount}
          </h6>
          <p
            className={style.rcv}
            style={{
              width: "100%",
            }}
          >
            {history.payment_mode?.toLowerCase() === "offline"
              ? history?.fee_receipt?.fee_payment_mode
                ? `${history?.fee_receipt?.fee_payment_mode}`
                : "Offline - No Receipt"
              : history.payment_mode?.toLowerCase() === "online"
              ? history?.fee_receipt?.fee_payment_mode
                ? `${history?.fee_receipt?.fee_payment_mode}`
                : "Online - No Receipt"
              : history?.payment_mode}
          </p>
        </div>
      </div>
    </>
  );
};

export default OtherTransactionHistoryCard;
