import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { useCallback } from "react";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import NormalOtherFees from "./NormalOtherFees/NormalOtherFees";
import SubjectOtherFees from "./SubjectOtherFees/SubjectOtherFees";
import OtherTransactionHistory from "./OtherTransactionHistory/OtherTransactionHistory";
const FinanceOtherFees = ({ fid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance`
        : `/q/${params.username}/member/staff/finance`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("other_fees")}</h6>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/finance?type=other-fees${tab.urlKey}`
                  : `/q/${params.username}/member/staff/finance?type=other-fees${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />

        {searchParams.get("n") === "subject" ? (
          <SubjectOtherFees fid={fid} instituteId={instituteId} />
        ) : searchParams.get("n") === "history" ? (
          <OtherTransactionHistory fid={fid} instituteId={instituteId} />
        ) : searchParams.get("n") === "normal" ? (
          <NormalOtherFees fid={fid} instituteId={instituteId} />
        ) : (
          <NormalOtherFees fid={fid} instituteId={instituteId} />
          // <OtherOutsandingFees fid={fid} instituteId={instituteId} />
        )}
      </UserMemberContainerWrapper>
    </>
  );
};

export default FinanceOtherFees;
const tabList = [
  // {
  //   label: "outstanding_fees",
  //   id: "outstanding_fees",
  //   urlKey: "",
  //   groupIs: ["", null],
  // },
  {
    label: "normal_other_fees",
    id: "normal",
    urlKey: "",
    groupIs: ["", null],
  },

  // {
  //   label: "normal_other_fees",
  //   id: "normal",
  //   urlKey: "&n=normal",
  //   groupIs: ["normal"],
  // },
  {
    label: "subject_other_fees",
    id: "subject",
    urlKey: "&n=subject",
    groupIs: ["subject"],
  },
  {
    label: "transaction_history",
    id: "history",
    urlKey: "&n=history",
    groupIs: ["history"],
  },
];
