import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useFinanceOtherFeesTransaction } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import style from "../../FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import OtherTransactionHistoryCard from "./OtherTransactionHistoryCard";
const OtherTransactionHistory = ({ fid, instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { financeOtherFeesTransaction, financeOtherFeesTransactionLoading } =
    useFinanceOtherFeesTransaction({
      data: {
        uid: instituteId,
        page: page,
        limit: 10,
        search: search,
        filter: "",
      },
      skip: !fid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeOtherFeesTransaction?.list?.length) {
      let obj = nextPage(financeOtherFeesTransaction?.list?.length);
      setState(() => obj);
    }
  }, [financeOtherFeesTransaction?.list?.length]);
  const onSearchEvent = (e) => {
    if (
      !financeOtherFeesTransactionLoading &&
      financeOtherFeesTransaction?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {financeOtherFeesTransaction?.list?.map((history, index) =>
          financeOtherFeesTransaction?.list?.length === index + 1 ? (
            <div key={history?._id} ref={ref}>
              <OtherTransactionHistoryCard history={history} />
            </div>
          ) : (
            <OtherTransactionHistoryCard history={history} key={history?._id} />
          )
        )}

        {financeOtherFeesTransactionLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          financeOtherFeesTransaction?.list?.length === 0 && (
            <EmptyMessage
              title="No receipts found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          financeOtherFeesTransaction?.list?.length === 0 && (
            <EmptyMessage
              title="No receipts added."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default OtherTransactionHistory;
