import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateButton from "../../../../Ui/Button/CreateButton";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../Income/Income.module.css";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
const UpdateOtherFees = ({ onClose, onEditAction, menuData }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [state, setState] = useState({
    other_fees_name: "",
  });

  useEffect(() => {
    if (menuData?.other_fees_name) {
      setState((prev) => ({
        ...prev,
        other_fees_name: menuData?.other_fees_name,
      }));
    }
  }, [menuData?.other_fees_name]);

  const onInputChange = (e) => {
    setState((pre) => ({
      ...pre,
      [e.target.name]: e.target.value,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      onEditAction(state);
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("update_other_fees")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("fees_name")}
              placeholder={t("fees_name_placeholder")}
              name={"other_fees_name"}
              value={state.other_fees_name}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["other_fees_name"]}
            />

            <CreateButton label="update" onAction={onAdd} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default UpdateOtherFees;
