// import style from "../InternalFees/InternalFees.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import FinanceOtherFeesCardMenu from "./FinanceOtherFeesCardMenu";
import { useState } from "react";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  useFinanceOtherFeeAddNonExistStudentCollect,
  useFinanceOtherFeeAddSingleStudentCollect,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import InstituteStudent from "../../InstituteStudent/InstituteStudent";
import FinanceOtherFeesCollectWhenAdd from "./FinanceOtherFeesCollect/FinanceOtherFeesCollectWhenAdd";
import OtherFeesAddNonExistingStudent from "./FinanceOtherFeesDetail/OtherFeesAddNonExistingStudent";
import QvipleLoader from "../../../Loader/QvipleLoader";

const FinanceOtherFeesCard = ({
  fees,
  onRefetch,
  whichOtherFees,
  instituteId,
  financeId,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);

  const [step, setStep] = useState("");
  const [singleStudent, setSingleStudent] = useState(null);
  const [disabled, setDisabled] = useState(false);

  const [financeOtherFeeAddSingleStudentCollect] =
    useFinanceOtherFeeAddSingleStudentCollect();
  const [financeOtherFeeAddNonExistStudentCollect] =
    useFinanceOtherFeeAddNonExistStudentCollect();
  const onToggle = (e) => {
    if (e) e.stopPropagation();
    setOpen((pre) => !pre);
  };
  const onDetail = () => {
    if (fees?.student_name) {
    } else {
      let url =
        getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/finance?type=other-fees-detail`
          : `/q/${params.username}/member/staff/finance?type=other-fees-detail`;
      navigate(url, {
        state: {
          ...getQuery.state,
          feesId: fees?._id,
          feesName: fees?.other_fees_name ?? "",
          otherData: {
            paid: fees?.paid_students_count ?? 0,
            rmaining: fees?.remaining_students_count ?? 0,
            payable_amount: fees?.payable_amount ?? 0,
          },
        },
      });
    }
  };

  const onClose = () => {
    setStep("");
  };

  const onAddSingleStudentToggle = () => {
    setStep(1);
  };

  const onSelectSingleStudent = (val) => {
    setSingleStudent(val);
    setStep(2);
  };
  const onAddSingleStudent = (data) => {
    if (financeId && singleStudent?._id && fees?._id) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddSingleStudentCollect({
        fid: financeId,
        bodyParameter: {
          students: [singleStudent?._id],
          ofid: fees?._id,
          ...data,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          setSingleStudent(null);
          onClose();
        })
        .catch({});
    }
  };
  const onAddNonStudentToggle = () => {
    setStep(3);
  };
  const onEnterStudentData = (val) => {
    setSingleStudent(val);
    setStep(4);
  };
  const onAddNonExistingSingleStudent = (data) => {
    if (financeId && singleStudent?.student_name && fees?._id) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddNonExistStudentCollect({
        fid: financeId,
        bodyParameter: {
          ...singleStudent,
          ofid: fees?._id,
          ...data,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          setSingleStudent(null);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <div className={style.sf_card} onClick={onDetail}>
        <div className={style.sf_card_contatiner}>
          <img
            src={`${assestsUserFinanceUrl}/fees-card.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              width: "2.5rem",
              height: "2.5rem",
              borderRadius: "0",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>{fees?.other_fees_name ?? ""}</h6>
            <p>
              {t("amount")} {": "}
              {fees?.payable_amount ?? ""}
            </p>
            {whichOtherFees === "Other_fees" ? (
              <p>
                {t("bank_name")} {": "}
                {fees?.bank_account?.finance_bank_account_name ?? ""}
              </p>
            ) : (
              <p>
                {t("fees_structure")} {": "}
                {fees?.fee_structure?.unique_structure_name ?? ""}
              </p>
            )}
            {fees?.student_name ? (
              <p>
                {t("student_name")}
                <span
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {" "}
                  {fees?.student_name ?? ""}
                </span>
              </p>
            ) : null}
          </div>
        </div>

        <div className={style.sf_card_contatiner}>
          <CreateButton
            label="add_single_student"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
              minWidth: "fit-content",
            }}
            onAction={onAddSingleStudentToggle}
          />
          <CreateButton
            label="add_non_existing_student"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
              minWidth: "fit-content",
            }}
            onAction={onAddNonStudentToggle}
          />
          {fees?.student_name ? (
            fees?.fee_receipt?.receipt_file && (
              <a
                href={`${pdfCdnUrl}/${fees?.fee_receipt?.receipt_file}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src="/images/members/receipt-icon.svg"
                  alt="receipt icon"
                />
              </a>
            )
          ) : (
            <img
              src={`${assestsUserFinanceUrl}/menu.svg`}
              alt="menu icon"
              onClick={onToggle}
              style={{
                height: "1.5rem",
                width: "1.5rem",
              }}
            />
          )}
        </div>
      </div>

      {step === 1 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onClose}
          instituteId={instituteId}
          onSelectStudent={onSelectSingleStudent}
          selectedId={singleStudent?._id}
        />
      ) : step === 2 ? (
        <FinanceOtherFeesCollectWhenAdd
          onClose={onClose}
          onParentClose={onClose}
          instituteId={instituteId}
          onGrabCollectFees={onAddSingleStudent}
          singleStudent={singleStudent}
          isOtherFees="OTHER_FEES"
          applicant={{
            head: fees?.payable_amount,
          }}
        />
      ) : step === 3 ? (
        <OtherFeesAddNonExistingStudent
          onClose={onClose}
          singleStudent={singleStudent}
          onNext={onEnterStudentData}
        />
      ) : step === 4 ? (
        <FinanceOtherFeesCollectWhenAdd
          onClose={() => setStep(3)}
          onParentClose={() => {}}
          instituteId={instituteId}
          onGrabCollectFees={onAddNonExistingSingleStudent}
          singleStudent={singleStudent}
          isOtherFees="OTHER_FEES"
          isNonExisting
          applicant={{
            head: fees?.payable_amount,
          }}
        />
      ) : null}

      {disabled && <QvipleLoader />}
      {open && (
        <FinanceOtherFeesCardMenu
          onClose={onToggle}
          menuData={fees}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default FinanceOtherFeesCard;
